body {
  /* margin: 0;
  padding: 0; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-linear-gradient(rgb(24, 163, 218), rgb(70, 78, 148));
  background-image: linear-gradient(rgb(24, 163, 218), rgb(70, 78, 148));
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.contentArea{
    display: flex;
}

.leftNav{
    flex: 0 0 18%;
}

.mainContent{
    flex: 1 1; /* instead we can put as value 1 */
    margin-left: 10px;
}
.headerContainer{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 20px;
}

.flexItem{
    display: inline;
    background-color: white;
    padding: 10px;
    text-align: center; 
    border-radius: 10px;
    cursor: pointer;
}
.leftNavCont{
    color:white;
    margin: 0;
    background-color: rgb(205, 50, 50);
    display: flex;
    flex-direction: column;
    height: 500px;
    border-radius: 20px;
    justify-content: space-evenly;
}
.topic{
    text-decoration: none;
    list-style: none;
}
.mainContent{
    background-color: rgb(0, 233, 214);
    color: white;
    border-radius: 20px;
}
